.about-container{
  background-image: linear-gradient(to right, #b5e3fb, #f4faff, #b5e3fb);
}
.about-section{
  font-weight: bold;
  padding-top: 100px;
}
.about-content{
  display: flex;
  margin: 0 auto 80px auto;
  max-width: 900px;
  padding: 0 20px;
  flex-direction: column;
}
.about-logo{
  text-align: center;
  margin-bottom: 30px;
}
.about-logo > img{
  width: 200px;
}
.about-description{
  color: #d6554f;
}
.about-descriptionTitle{
  margin-bottom: 0;
}
.about-building > img{
  width: 100%;
  display: block;
}

@media(min-width: 480px){
  .about-content{
    flex-direction: row;
  }
  .about-logo{
    margin-right: 50px;
    margin-bottom: 0;
  }
}

